import {ProductOptionsType} from 'components/Form/RewardItem'
import {makeAutoObservable} from 'mobx'

export default class RewardModel {
  store: any
  field: string
  placeholder: string
  product: string
  productOptions: ProductOptionsType[]
  noCount: boolean
  count: number
  constructor(store: any, data: any) {
    makeAutoObservable(this)
    this.store = store
    this.field = data.field
    this.placeholder = data.placeholder
    this.productOptions = data.productOptions
    this.noCount = data.noCount
    this.count = 1
    this.product = ''
  }

  remove = () => {
    this.store.rewards.remove(this)
  }

  setProduct = (p: string) => {
    this.product = p
  }

  setCount = (count: number) => {
    this.count = count
  }

  toJSON = () => {
    if (!this.product) {
      return false
    }
    if (!this.store.rewardObj[this.field]) {
      this.store.rewardObj[this.field] = this.noCount ? [] : {}
    }

    if (this.noCount) {
      this.store.rewardObj[this.field].push(this.product)
    } else {
      this.store.rewardObj[this.field] = {
        ...this.store.rewardObj[this.field],
        [this.product]: this.count,
      }
    }
  }
}
