import React, {useState} from 'react'
import {observer} from 'mobx-react-lite'
import BasicLayout from '../../components/Layout/BasicLayout'
import TitlePanel from '../../components/Layout/TitlePanel'
import Content from '../../components/Layout/Content'
import ServerSearch from '../../components/Search/ServerSearch'
import BasicTabs from '../../components/Tabs/Tabs'
import Title from '../../components/Text/Title'
import {Button, DatePicker, Form, Input, Select, Space} from 'antd'
import StyledButton from '../../components/Button/StyledButton'
import {DownOutlined} from '@ant-design/icons'
import RewardForm from 'components/Form/RewardForm'
import moment from 'moment'
import {useStores} from 'stores'

const MailPage = () => {
  const [serverId, setServerId] = useState('')
  const [receiverType, setReceiverType] = useState('pids')
  const {
    appStore: {sendMailReward},
  } = useStores()
  const send = (values: any) => {
    const {pid, time, ...restValues} = values

    const data: any = {}

    if (receiverType === 'pids') {
      data.pids = pid
    } else {
      data.wid = serverId
    }

    if (time) {
      restValues.time = moment(time).format('YYYY-MM-DD HH:mm:ss')
    }

    return sendMailReward({data, value: restValues}).then(() => {
      alert('우편 발송 완료!')
    })
  }
  return (
    <BasicLayout>
      <TitlePanel>메시지 관리</TitlePanel>
      <Content>
        <ServerSearch onChange={(w: any, c: any) => c && setServerId(w._id)} />
        <Title style={{marginTop: 40}}>우편 발송 및 조회</Title>
        <BasicTabs tabs={['우편 발송']}>
          <div style={{width: 800}}>
            <Form
              requiredMark={false}
              colon={false}
              labelCol={{xs: 5}}
              wrapperCol={{xs: 19}}
              onFinish={send}
              initialValues={{target_type: 'one'}}
            >
              <Form.Item
                label={'제목'}
                name={'title'}
                style={{marginBottom: 10}}
                rules={[{required: true, message: '제목을 입력해주세요.'}]}
              >
                <Input />
              </Form.Item>
              <Form.Item
                label={'시간'}
                name={'time'}
                style={{marginBottom: 10}}
                rules={[{required: true, message: '시간을 입력해주세요.'}]}
              >
                <DatePicker showTime style={{width: 150}} />
              </Form.Item>
              <Form.Item
                label={'서비스 국가'}
                name={'country'}
                style={{marginBottom: 10}}
              >
                <Select style={{width: 150}}>
                  <Select.Option value="korea">전체/한국</Select.Option>
                </Select>
              </Form.Item>
              <Form.Item
                label={'언어'}
                name={'lang'}
                style={{marginBottom: 30}}
              >
                <Select style={{width: 150}}>
                  <Select.Option value="korean">한국어</Select.Option>
                </Select>
              </Form.Item>
              <Form.Item
                label={'대상'}
                name={'target_type'}
                style={{marginBottom: 10}}
              >
                <Space>
                  <div
                    style={{marginRight: 15}}
                    onClick={() => setReceiverType('pids')}
                  >
                    <Button
                      type={receiverType === 'pids' ? 'primary' : 'default'}
                      style={{borderRadius: 5, marginRight: 10}}
                      size={'small'}
                    >
                      <DownOutlined />
                    </Button>
                    다수의 플레이어
                  </div>
                  <div onClick={() => setReceiverType('wid')}>
                    <Button
                      type={receiverType === 'wid' ? 'primary' : 'default'}
                      style={{borderRadius: 5, marginRight: 10}}
                      size={'small'}
                    >
                      <DownOutlined />
                    </Button>
                    전체 플레이어(월드 서버 단위)
                  </div>
                </Space>
              </Form.Item>
              <Form.Item label={'PID'} name={'pid'} style={{marginBottom: 10}}>
                <Input.TextArea />
              </Form.Item>
              <Form.Item
                label={'상세 내용'}
                name={'body'}
                style={{marginBottom: 30}}
              >
                <Input.TextArea />
              </Form.Item>
              <RewardForm />
              <div style={{textAlign: 'right', marginBottom: 20}}>
                <StyledButton purple type={'submit'}>
                  우편 발송
                </StyledButton>
              </div>
            </Form>
          </div>
        </BasicTabs>
      </Content>
    </BasicLayout>
  )
}

export default observer(MailPage)
