import React, {useState} from 'react'
import {DatePicker, Space, Select} from 'antd'
import StyledButton from 'components/Button/StyledButton'
import {getActionLog} from '../../apis/player'
import moment from 'moment'
import ReactJson from 'react-json-view'

const Log: React.FC<{pid: string}> = ({pid}) => {
  const [startDate, setStateDate] = useState()
  const [endDate, setEndDate] = useState()
  const [logType, setLogType] = useState('')
  const [log, setLog] = useState({})

  const getLog = () => {
    return getActionLog({
      pid,
      from: startDate && moment(startDate).valueOf(),
      to: endDate && moment(endDate).valueOf(),
      type: logType,
    }).then(({data}) => {
      setLog(data)
    })
  }
  return (
    <div>
      <Space style={{marginTop: 15, marginBottom: 15}}>
        기간 설정
        <DatePicker
          onChange={(date: any, dateString: string) => setStateDate(date)}
        />
        ~
        <DatePicker
          onChange={(date: any, dateString: string) => setEndDate(date)}
        />
      </Space>
      <Space style={{marginBottom: 10}}>
        로그 항목
        <Select
          style={{width: 200}}
          value={logType}
          onChange={(v) => setLogType(v)}
        >
          <Select.Option value={''}>전체</Select.Option>
          <Select.Option value={'onLeagueReward'}>
            투기장 보상 이력
          </Select.Option>
          <Select.Option value={'buyBuilingShop'}>
            유료 상점 상품 구매 이력
          </Select.Option>
          <Select.Option value={'finishedStage'}>
            모험 스테이지 진행 이력(종료)
          </Select.Option>
          <Select.Option value={'gachaCharacter'}>
            여관 캐릭터 소환 이력
          </Select.Option>
          <Select.Option value={'getMissionReward'}>
            임무 보상 획득 이력
          </Select.Option>
          <Select.Option value={'getRewardsFromMsg'}>
            우편 보상 수령 이력
          </Select.Option>
          <Select.Option value={'openCharacterTicket'}>
            캐릭터 소환권 영웅 획득 이력
          </Select.Option>
          <Select.Option value={'removeMsgFromMid'}>
            우편 삭제 이력
          </Select.Option>
        </Select>
        <StyledButton blue onClick={getLog}>
          검색
        </StyledButton>
      </Space>
      <ReactJson src={log} />
    </div>
  )
}

export default Log
