import React, {useEffect, useState} from 'react'
import {InputNumber, Select, Space} from 'antd'
import RewardModel from 'stores/models/RewardModel'
import {MinusCircleOutlined} from '@ant-design/icons'
import {observer} from 'mobx-react-lite'

type OptionsType = {label: string; value: string}[]
export interface ProductOptionsType {
  maxCount?: number
  label: string
  value: string
  options?: OptionsType
  optionType?: string
}

const RewardItem: React.FC<{
  reward: RewardModel
}> = ({reward}) => {
  const {
    placeholder,
    productOptions,
    noCount,
    product,
    setProduct,
    count,
    setCount,
  } = reward

  const selectedOption = productOptions.find((po) => po.value === product)
  return (
    <div
      style={{
        marginBottom: 5,
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'baseline',
      }}
    >
      <Space>
        <Select
          showSearch
          onChange={(v: string) => setProduct(v)}
          placeholder={placeholder}
          style={{width: 250}}
          filterOption={(input, option) => {
            return (
              option?.children.toLowerCase().indexOf(input.toLowerCase()) >=
                0 ||
              option?.value.toLowerCase().indexOf(input.toLowerCase()) >= 0
            )
          }}
        >
          {productOptions.map((product: any, idx: number) => {
            const value = String(product.value)
            return (
              <Select.Option key={value || idx} value={value}>
                {product.label}
              </Select.Option>
            )
          })}
        </Select>
        {!noCount && (
          <Space>
            <InputNumber
              max={
                selectedOption
                  ? selectedOption.maxCount
                  : Number.MAX_SAFE_INTEGER
              }
              value={count}
              onChange={(v) => setCount(v)}
            />
            <span>수량</span>
          </Space>
        )}
      </Space>
      <MinusCircleOutlined onClick={reward.remove} />
    </div>
  )
}

export default observer(RewardItem)
