import React, {useState} from 'react'
import {Button, DatePicker, Form, Input, Select, Space, Checkbox} from 'antd'
import BasicTabs from '../Tabs/Tabs'
import {DownOutlined, UpOutlined} from '@ant-design/icons'
import RewardItem from './RewardItem'
import StyledButton from '../Button/StyledButton'
import {observer} from 'mobx-react-lite'
import {useStores} from '../../stores'
import {Tiers} from '../player/Equipments'
import {ResourceType} from '../player/Resources'
import RewardModel from 'stores/models/RewardModel'

const RewardTiers = [...Tiers, '세트']

const MailForm: React.FC<{players: any; onClose: any}> = ({
  players = [],
  onClose = () => {},
}) => {
  const [visibleReward, setVisibleReward] = useState(true)
  const [isAllPlayers, setIsAllPlayers] = useState(false)
  const {
    appStore: {
      gameItemData,
      equipmentKeyList,
      heroData,
      rewards,
      addReward,
      sendMailReward,
      server: {cid},
    },
  } = useStores()
  const send = (value: any) => {
    let data: any = {pids: players.map((p: any) => p.pid).join(',')}

    if (isAllPlayers) {
      data = {cid}
    }
    return sendMailReward({data, value}).then(() => {
      alert('우편 보내기 완료')
      onClose()
    })
  }

  const add = (type: string) => {
    let rewardProps: any = null
    switch (type) {
      case 'item':
        rewardProps = {
          productOptions: Object.keys(gameItemData).map(
            (key) => gameItemData[key]
          ),
          placeholder: '게임아이템',
          field: 'gameItemReward',
        }
        break
      case 'hero':
        rewardProps = {
          noCount: true,
          productOptions: Object.keys(heroData).map((key) => heroData[key]),
          placeholder: '영웅',
          field: 'characterKeys',
        }
        break
      case 'equipment':
        rewardProps = {
          noCount: true,
          productOptions: Object.keys(equipmentKeyList).map((key) => {
            return {
              ...equipmentKeyList[key],
              label: `${equipmentKeyList[key].label}(${
                RewardTiers[equipmentKeyList[key].tier - 1]
              })`,
            }
          }),
          placeholder: '장비',
          field: 'equipmentKeyList',
        }
        break
      case 'resource':
        rewardProps = {
          productOptions: ResourceType.map((r) => ({
            ...r,
            label: r.name,
          })),
          placeholder: '재화',
          field: 'userResourceReward',
        }
        break
    }

    addReward(rewardProps)
  }

  return (
    <BasicTabs tabs={['일반 우편']} style={{marginTop: 10}}>
      <Form
        requiredMark={false}
        colon={false}
        labelCol={{xs: 5}}
        wrapperCol={{xs: 19}}
        onFinish={send}
      >
        <Form.Item
          label={'제목'}
          name={'title'}
          style={{marginBottom: 10}}
          rules={[{required: true, message: '제목을 입력해주세요.'}]}
        >
          <Input />
        </Form.Item>
        <Form.Item label={'대상'} style={{marginBottom: 10}}>
          <div style={{color: '#40A3FF'}}>
            {isAllPlayers ? (
              <span style={{color: '#ff4d4f'}}>
                전체 플레이어(월드 서버 단위) - 선택됨! / 신중하게 선택하세요!
              </span>
            ) : (
              `선택된 플레이어 ${players.length}명`
            )}
          </div>
          <div>
            <Checkbox
              checked={isAllPlayers}
              onChange={(e) => setIsAllPlayers(e.target.checked)}
            >
              전체 플레이어
            </Checkbox>
          </div>
        </Form.Item>
        <Form.Item
          label={'시간'}
          name={'time'}
          style={{marginBottom: 10}}
          rules={[{required: true, message: '시간을 입력해주세요.'}]}
        >
          <DatePicker style={{width: 150}} />
        </Form.Item>
        <Form.Item
          label={'서비스 국가'}
          name={'country'}
          style={{marginBottom: 10}}
        >
          <Select style={{width: 150}}>
            <Select.Option value="korea">전체/한국</Select.Option>
          </Select>
        </Form.Item>
        <Form.Item label={'언어'} name={'lang'} style={{marginBottom: 30}}>
          <Select style={{width: 150}}>
            <Select.Option value="korean">한국어</Select.Option>
          </Select>
        </Form.Item>
        <Form.Item label={'상세 내용'} name={'body'} style={{marginBottom: 30}}>
          <Input.TextArea />
        </Form.Item>
        <Form.Item wrapperCol={{offset: 5, span: 19}} style={{marginBottom: 0}}>
          <>
            <Button
              style={{borderRadius: 5, marginRight: 10}}
              size={'small'}
              onClick={() => setVisibleReward(!visibleReward)}
            >
              {visibleReward ? <DownOutlined /> : <UpOutlined />}
            </Button>
            보상 제외
          </>
        </Form.Item>
        {visibleReward && (
          <>
            <Form.Item label={' '} style={{marginTop: 30, marginBottom: 15}}>
              <Space>
                보상 항목 추가
                <Button
                  type={'primary'}
                  style={{borderRadius: 4}}
                  onClick={() => add('resource')}
                >
                  +재화
                </Button>
                <Button
                  type={'primary'}
                  style={{borderRadius: 4}}
                  onClick={() => add('item')}
                >
                  +아이템
                </Button>
                <Button
                  type={'primary'}
                  style={{borderRadius: 4}}
                  onClick={() => add('equipment')}
                >
                  +장비
                </Button>
                <Button
                  type={'primary'}
                  style={{borderRadius: 4}}
                  onClick={() => add('hero')}
                >
                  +영웅
                </Button>
              </Space>
            </Form.Item>
            <Form.Item label={'보상 내용'} style={{marginBottom: 15}}>
              {rewards.map((r: RewardModel, idx: number) => {
                return <RewardItem key={r.field + idx} reward={r} />
              })}
            </Form.Item>
          </>
        )}
        <div
          style={{
            borderTop: '1px solid #ccc',
            paddingTop: 10,
            marginTop: 30,
            textAlign: 'right',
          }}
        >
          <Space>
            <StyledButton purple type={'submit'}>
              우편 보내기
            </StyledButton>
            <StyledButton type={'button'} gray onClick={onClose}>
              닫기
            </StyledButton>
          </Space>
        </div>
      </Form>
    </BasicTabs>
  )
}

export default observer(MailForm)
