import React, {useState} from 'react'
import {Button, InputNumber, Space, Table, Tabs} from 'antd'
import PlayerModel from 'stores/models/PlayerModel'
import {SizeType} from 'antd/es/config-provider/SizeContext'

export const ResourceType = [
  {name: '골드', value: 'GP', maxCount: 100000},
  {name: '다이아', value: 'Diamond', maxCount: 10000},
  {name: '캐릭터경험치', value: 'ExpPoints'},
  {name: '영혼석', value: 'SoulStone', maxCount: 100000},
  {name: '럭키코인', value: 'LuckyCoin', maxCount: 1000},
]

export const ReturnResource: React.FC<{
  data: any
  currentPlayer: PlayerModel
  isInputNumber?: boolean
  refineData: any
  resourceName: string
}> = ({
  data,
  currentPlayer,
  isInputNumber = false,
  refineData,
  resourceName,
}) => {
  const [value, onChange] = useState<string | number>()
  const [msg, setMsg] = useState<string>('')
  const {label, amount} = data
  const returnResource = () => {
    if (window.confirm(`${label} 회수가 맞습니까?`)) {
      currentPlayer.returnResource(refineData(data, value)).then(() => {
        setMsg('회수 우편 발송 완료')
      })
    }
  }

  return (
    <Space>
      {isInputNumber && (
        <InputNumber
          value={value}
          max={amount}
          onChange={onChange}
          style={{color: '#ff4d4f'}}
          formatter={(value) =>
            `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')
          }
          parser={(value: string | undefined) =>
            (value || '').replace(/(,*)/g, '')
          }
        />
      )}
      <Button type="primary" danger onClick={returnResource}>
        {resourceName} 회수
      </Button>
      <div style={{color: '#ff4d4f'}}>{msg}</div>
    </Space>
  )
}

const Resources: React.FC<{
  currentPlayer?: PlayerModel
  userResources: any
  size?: SizeType
}> = ({currentPlayer, userResources = {}, size}) => {
  const data = ResourceType.map((resource, idx: number) => ({
    number: idx + 1,
    key: idx + 1,
    type: resource.name,
    label: resource.name,
    count: userResources[resource.value],
    value: resource.value,
    amount: userResources[resource.value],
  }))

  const columns = [
    {title: '넘버', dataIndex: 'number'},
    {title: '재화 종류', dataIndex: 'type'},
    {title: '보유 수량', dataIndex: 'count'},
    {
      title: '비고',
      dataIndex: '',
      render: (_: any, data: any) =>
        currentPlayer && (
          <ReturnResource
            resourceName={'재화'}
            refineData={(data: any, value: any) => {
              return {
                userResourceReward: {
                  [data.value]: value,
                },
              }
            }}
            data={data}
            currentPlayer={currentPlayer}
            isInputNumber
          />
        ),
    },
  ]
  return (
    <div>
      <Tabs defaultActiveKey="1" onChange={() => {}} size={size}>
        <Tabs.TabPane tab="보유 재화" key="보유 재화">
          <Table columns={columns} dataSource={data} size={size} />
        </Tabs.TabPane>
      </Tabs>
    </div>
  )
}

export default Resources
